.slider {
  &__photo {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__buttons-list {
    height: 64px;
    width: 162px;
    display: flex;
    position: absolute;
    top: 50%;
    transform: translate(-50%,-50%);
    border-radius: 50px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%,
    rgba(255, 255, 255, 0) 100%),
      $c-yellow;

    @media screen and (max-width: 1020px) {
      & {
        transform: translate(-50%,-50%) scale(0.5);
      }
    }
  }

  &__button {
    box-sizing: border-box;
    height: 100%;
    width: 50%;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    border-width: 0;
    background-color: transparent;
    background-image: url("../images/slider/arrow-left.svg");
    background-repeat: no-repeat;
    background-position: center;
    transition: background-color $t-transition;
  }

  &__button:hover {
    background-color: #fc9904;
  }

  &__button--reverse {
    transform: rotate(180deg);
  }

  &__caption {
    position: absolute;
    bottom: 86px;
    left: 98px;

    @media screen and (max-width: 1020px) {
      bottom: 40px;
      left: 60px;
    }

    @media screen and (max-width: 600px) {
      bottom: 20px;
      left: 30px;
    }
  }

  &__caption-title {
    font-size: 12.6px;
    font-weight: 800;
    line-height: 150%;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: $c-title;

    @media screen and (max-width: 600px) {
      font-size: 10px;
    }
  }

  &__caption-text {
    font-size: 19.6px;
    font-weight: 900;
    line-height: 140%;
    color: $c-text-white;
    width: 264px;

    @media screen and (max-width: 600px) {
      font-size: 10px;
      width: 150px;
    }
  }
}
